<template>
    <div class="cont">
        <div class="cont-title">
            <a class="bold">收藏的商品&nbsp;({{collectList.length}})</a>
        </div>
        <div class="cont-table clearfix">
            <div v-if="collectList.length!==0" :class="['cont-item','fl',{'margin-no' :(key+1)%4 ===0 }]" v-for="(item,key) in collectList.slice(page*limit,(page+1)*limit)">
                <a :href="`/goods.html#/${item.product_id}`">
                    <img :src="item.default_img | img" alt="">
                    <p class="ac">{{item.product_name}}</p>
                </a>
                <p class="price ac bold" >{{item|exch(storage.getStorage('exchange'))}} <span style="font-size: 14px;color: black;">{{storage.getStorage('exchange').unit}}</span></p>
                <div class="bottom-btn row">
                    <div class="col-6 ac cursor" @click="addCart(item.product_id)">加入购物车</div>
                    <div class="col-6 ac cursor" style="border-left: none" @click="item.confirm = true">取消收藏</div>
                </div>
                <div class="cont-mask" v-if="item.confirm">
                    <p class="ac" style="color: white;
                       margin-top: 114px;
                ">确定不要我了么?</p>
                    <div class="bottom-btn row">
                        <div class="col-6 ac cursor confirm"
                             style="background-color: #ff0077;
                                    border: none;
                                    color: white;
                                    "
                             @click="delCollect(item.product_id)">确定</div>
                        <div class="col-6 ac cursor confirm"
                             style="background-color: white;
                             border: none;
                             color: black;
                        " @click="item.confirm = false">取消</div>
                    </div>
                </div>
            </div>
            <div v-if="collectList.length===0" class="ac" style="padding-top: 50px">
                <img src="../assets/img/nothing/collection_1.png" alt="">
                <p>咦，一个收藏的宝贝也没有，赶紧去<a href="/grade.html" style="color: #66ccff">商品中心</a>逛逛吧！</p>
            </div>
        </div>
        <div class="page-btn clearfix">
            <div class="cursor ac fr" @click="PageTurning(1)">下一页</div>
            <div class="fr page-info">{{page+1}}</div>
            <div class="cursor ac fr" @click="PageTurning(-1)">上一页</div>
        </div>
    </div>
</template>

<script>
    let display = {
        showRecommend: true,
        nav: 'default',
        header: 'default'
    };
    export default {
        name: "cont",
        metaInfo: {
            title: '我的收藏-个人中心', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                collectList: [],
                limit:20,
                page:0
            }
        },
        async created() {
            this.$store.dispatch('layout/Change', display);
            this.PostCollectList()
        },
        methods: {
            PostCollectList() {
                this.bus.$emit('loading','加载中',false);
                this.collectList = [];
                this.yum('api/member/collectList').then(
                    res => {
                        if (res.data.status === 200) {
                            setTimeout(()=>{
                                this.bus.$emit('loading','加载中',true);
                            },300);
                             res.data.data.forEach(item=>{
                                 this.collectList.push(Object.assign(item,{confirm:false}))
                             })
                        }else{
                            setTimeout(()=>{
                                this.bus.$emit('loading','加载中',true);
                                this.bus.$emit('notice',res.data.msg)
                            },300);
                        }
                    }
                ).catch(err=>{
                    this.bus.$emit('notice','网络错误！')
                })
            },
            delCollect(id) {
                this.yum('api/member/delCollect', {product_id: id}).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('notice', res.data.msg, 500);
                        this.PostCollectList()
                    }
                })
            },
            addCart(id) {
                let data = {
                    product_id: id,
                    status: 0,
                    nums: 1
                };
                this.yum('api/member/addCart', data).then(res => {
                    if (res.data.status === 200) {
                        this.bus.$emit('topNav');
                        this.bus.$emit('notice', res.data.msg, 500)
                    }
                })
            },
            PageTurning(a){
                document.documentElement.scrollTop = 0;
                if (a>0){
                    this.page===this.TotalPage?this.page=this.page:this.page+=a;
                }else{
                    this.page===0?this.page=0:this.page+=a
                }
            }
        },
        computed:{
            TotalPage(){
                let page = Math.ceil(this.collectList.length/this.limit)-1;
                return page
            }
        }
    }
</script>

<style scoped lang="stylus">
    div.cont.router
        margin-left 10px
        width 830px

    .cont-title
        height 50px
        line-height 50px
        padding 0 20px
        background-color white
        box-shadow 0 0 8px #bbb
        a
            color #ff0077
    .cont-table
        position relative
        margin-top 10px
        .cont-item
            position relative
            width 200px
            height 277px
            margin-right 10px
            margin-bottom 10px
            background-color white
            .cont-mask
                position absolute
                width 200px
                height 277px
                background-color rgba(0,0,0,0.6)
                top 0
                left 0
                transition all 0.3s
                user-select none
                .confirm
                    height 30px
            &.margin-no
                margin-right 0
            img
                display block
                width 140px
                height 140px
                margin 30px auto 0
            p
                margin 20px 20px 0
                width 160px
                overflow hidden
                text-overflow ellipsis
                white-space nowrap
            p.price
                margin-top 10px
                font-size 16px
                color #ff0077
            .bottom-btn
                position absolute
                height 30px
                line-height 28px
                bottom 0
                box-sizing border-box
                div.col-6.ac
                    border 1px solid #ccc
                    box-sizing border-box
                    user-select none
                    &:hover
                        color white
                        border-color #ff0077
                        background-color #ff0077
    .page-btn
        display block
        margin 50px auto
        div.cursor.ac
            display inline-block
            margin-left 20px
            width 100px
            height 28px
            line-height 26px
            border 1px solid #ccc
            background-color white
            border-radius 4px
        div.page-info
            height 28px
            line-height 28px
            margin-left 20px

</style>